@charset "UTF-8";
/** CSS Grid Layoutで複数の行を指定するmixin */
/** CSS Grid Layoutで複数の列を指定するmixin */
html, body {
  height: 100%;
}

body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, "游ゴシック Medium", "Yu Gothic Medium", "游ゴシック体", YuGothic, "ヒラギノ角ゴ Pro W3", "Hiragino Kaku Gothic ProN", "メイリオ", Meiryo, sans-serif;
  font-feature-settings: "palt";
}

.my-grid {
  display: grid;
  grid-template-columns: 200px 1fr;
  grid-template-rows: 50px 1fr 50px;
  background: red;
  width: 100%;
  height: 100%;
}

.my-grid-item {
  display: flex;
  justify-content: center;
  align-items: center;
  color: white;
  font-size: 2.0rem;
  font-weight: bold;
  letter-spacing: 0.1em;
}

header {
  grid-column-start: 1;
  grid-column-end: 3;
  -ms-grid-column-span: 2;
  grid-row: 1;
  background: #3498db;
}

aside {
  grid-column: 1;
  grid-row-start: 2;
  grid-row-end: 4;
  -ms-grid-row-span: 2;
  background: #e67e22;
}

main {
  grid-column: 2;
  grid-row: 2;
  background: #2ecc71;
}

footer {
  grid-column: 2;
  grid-row: 3;
  background: #95a5a6;
}
